.nav-container{
    width: 100vw;
    height: 10vh;
    border-bottom: 2px solid #f1f1f1;
    padding: 0 20px;
    display: flex;
    justify-content: flex-end;
}


.navbar-list{
    list-style: none;
    display: flex;
    flex-flow: row nowrap;
}


.navbar-option{
    padding: 18px 10px;
}


.burger-outer{
   width: 2rem;
   height: 2rem;
   position: fixed;
   top:15px;
   right: 20px;
   z-index: 20;
   display: none


}

.innner-burger{
    width:2rem;
    height: 0.25rem;
    border-radius: 10px;
    transform-origin: 1px;
    transition: all 0.3s linear;
}


@media( max-width:768px){
    .navbar-list{
    background-color: black;
      flex-flow: column nowrap;
      position: fixed;
      top: 0;
      right: 0;
      height: 100vh;
      width: 150px;
      padding-top: 3.5rem;
      transition: transform 0.3s ease-in-out;
    }

    li{
        color: #fff;
    }
}

@media( max-width:768px){
    .burger-outer{
        display: flex;
        justify-content: space-around;
        flex-flow:  column nowrap ;
    }
}